const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.last')
/* ÂàùÂßãÊó∂‰ªé  LocalStorage ‰∏≠ËØªÂèñÂØπÂ∫îÁöÑ hash  */
const x = localStorage.getItem('x') || 'null'
// Â∞ÜËØªÂèñÁöÑÂ≠óÁ¨¶‰∏≤ Âèò‰∏∫ ÂØπË±°
const xObject = JSON.parse(x)
// hashMap ÂàùÂßãÂåñ‰øùÂ∫ïÂÄº
const hashMap = xObject || [{
  logo: 'A',
  url: 'https://www.acfun.cn',
  logoICO: 'https://www.acfun.cn/favicon.ico'
}, {
  logo: 'B',
  url: 'https://www.bilibili.com',
  logoICO: 'https://www.bilibili.com/favicon.ico'
}]
/* ÊòæÁ§∫ÈìæÊé• Áº©Áü≠ */
const simplifyUrl = (url) => {
  return url.replace('https://', '')
    .replace('http://', '')
    .replace('www.', '')
    .replace(/\/.*/, '') // Âà†Èô§‰ª• / ÂºÄÂ§¥ÁöÑÂÜÖÂÆπ Ë¥™Â©™ÂåπÈÖç
}
/* Êìç‰Ωú hashMap Ê∏≤ÊüìÈ°µÈù¢ */
const render = () => {
  /*  ÂÜçÊ¨°Ê∏≤Êüì hashMap ‰πãÂâçÂøÖÈ°ªÂÖà Ê∏ÖÁ©∫ÂéüÊù•ÁöÑ */
  // ÊâæÂà∞ ÁöÑÈô§‰∫Ü lastLi ‰πãÂ§ñÁöÑÊâÄÊúâ li
  $siteList.find('li:not(.last)').remove()
  hashMap.forEach((node, index) => {
    // console.log(node.logoICO)
    let showLogo = `<img src = ${node.logoICO}>`
    const $li = $(`
        <li>
          <div class="site">
            <div class="logo">${showLogo}</div>
            <div class="link">${simplifyUrl(node.url)}</div>
            <div class="close">
              <svg class="icon">
                <use xlink:href="#icon-close"></use>
              </svg>
            </div>
          </div>
        </li>
  `).insertBefore($lastLi)
    // Áî®JSÊìç‰Ωú ÁÇπÂáªË∑≥ËΩ¨
    $li.on('click', () => {
      // console.log(node.url);
      window.open(node.url, '_self')
    })
    // ÈòªÊ≠¢ÂÜíÊ≥°
    $li.on('click', '.close', (e) => {
      // console.log('ÈòªÊ≠¢ÂÜíÊ≥°')
      e.stopPropagation()
      // console.log(hashMap);
      // console.log(index);
      hashMap.splice(index, 1)
      render()
    })
  })
}
render()

/* ‰∫ã‰ª∂Â§ÑÁêÜ */
$('.addButton').on('click', () => {
  let url = window.prompt("ËØ∑ËæìÂÖ•ÁΩëÂùÄ")
  /* ÁΩëÂùÄÂêàÊ≥ïË°åÂà§Êñ≠ */
  if (url.indexOf("www.") !== 0 && url.indexOf("http") !== 0) {
    url = 'www.' + url
    console.log(url);
  }
  if (url.indexOf("http") !== 0) {
    url = 'https://' + url
    console.log(url);
  }
  // console.log(url)
  hashMap.push({
    logo: simplifyUrl(url)[0].toUpperCase(),
    url: url,
    logoICO: (url + '/favicon.ico')
  })
  render()
})

// Ëé∑ÂèñÁÑ¶ÁÇπ
window.onload = () => {
  // $('.searchInput').focus() <input autofocus>
  $('.searchForm').attr("action", "https://baidu.com/s")
}

// ÈÄÄÂá∫ÁΩëÁ´ô Áî®Êà∑ÂÖ≥Èó≠ÁΩëÁ´ôÂâçËß¶Âèë Â≠òÂà∞ localStorage Èáå
window.onbeforeunload = () => {
  // console.log('È°µÈù¢Ë¶ÅÂÖ≥Èó≠‰∫Ü') // ÂèØ‰ª•ÂºÄÂêØ Preserve log Êü•Áúã
  // // Â∞Ü ÂØπË±°Âèò‰∏∫ Â≠óÁ¨¶‰∏≤
  const string = JSON.stringify(hashMap)
  // console.log(typeof hashMap)
  // console.log(hashMap)
  // console.log(typeof string)
  // console.log(string)
  localStorage.setItem('x', string)
}

// ÁõëÂê¨ÈîÆÁõò‰∫ã‰ª∂ ÁÇπÂáªË∑≥ËΩ¨Áõ∏Â∫îÁΩëÁ´ô
$(document).on('keypress', (e) => {
  // console.log(e.keyCode)
  // console.log(e.key)
  /*
  const key = e.key
  ÂèòÈáèÂêç Âíå Â±ûÊÄßÂêç‰∏ÄÊ†∑
  ÂèØ‰ª•ÁÆÄÂÜô‰∏∫
  const {key} = e
  Ëß£ÊûÑËµãÂÄº
  */
  const {
    key
  } = e;
  // console.log(key);
  for (let i = 0; i < hashMap.length; i++) {
    if (hashMap[i].logo.toLowerCase() === key) {
      // console.log(hashMap[i].logo)
      // location.href = hashMap[i].url // Ê®°ÊãüÁî®Êà∑Âú®Âú∞ÂùÄÊ†èËæìÂÖ•
      window.open(hashMap[i].url, '_blank')
    }
  }
})

// ÊêúÁ¥¢Ê°Ü ÈîÆÁõò‰∫ã‰ª∂ ÈòªÊ≠¢ÂÜíÊ≥°
$('.searchForm').on('keypress', (e) => {
  e.stopPropagation()
});

